@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";
@import "../../variables";
@import "../../mixins";


.c-cookies {	
	display: block;
	width: 100%;
    margin-right: auto;
    margin-left: auto; 
}



.c-cookies-content{
    text-align: left;
    br{
        display: none;
    }
    @media (min-width:map-get($grid-breakpoints, 'md')){
        text-align: center;
        margin:0 auto;
        br{
            display: block;
        }
    }
    
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        max-width:900px;
    }
    &.c-cookies-content--align-left{
        text-align: left;
        @media (min-width:map-get($grid-breakpoints, 'md')){
            text-align: left;
    
        }
    }
}

.c-cookies-inner{
	width: 100%;
    margin-right: auto;
    margin-left: auto; 
    padding:20px 15px;
    font-size: 14px;
    color:#7d8a94;

    
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        padding-right: 0;
        padding-left: 0;
    }
}